* {
    box-sizing: border-box;
}

.game-mode {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    background-image: url(../../assets/images/zumbi.jpg);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100vh;
    background-color: #484848;
}

.container > a {
    cursor: default;
    text-decoration: none;
    color: red;
    text-shadow: 0px 4px 1px #000000;
}

.container p:first-child {
    text-align: center;
    font-size: 50px;
    font-family: blood;
}

.container > .buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
}
